<template>
  <main class="main" v-if="loader">
    <h2 class="title">Fake Posts</h2>
    <router-link class="home" to="/">Back to Home page</router-link>

    <div class="main__posts container">
      <div class="main__posts-item" v-for="post in posts" :key="post.id">
        <p class="main__posts-number">Number: {{ post.id }}</p>
        <p class="main__posts-title">Title: {{ post.title }}</p>
        <p class="main__posts-content">Content: {{ post.body }}</p>
      </div>
    </div>
  </main>

  <div class="main__loader" v-else>
    <div class="loader__el"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      posts: null,
      loader: false,
    };
  },
  methods: {
    async getUser() {
      try {
        const res = await fetch("https://jsonplaceholder.typicode.com/posts");
        const data = await res.json();
        this.posts = data;
      } catch (error) {}
    },
  },
  mounted() {
    setTimeout(() => {
      this.getUser();
      this.loader = true;
    }, 2000);
  },
};
</script>

