<template>
  <main class="main">
    <h2 class="title">Fake Albums</h2>
    <router-link class="home" to="/">Back to Home page</router-link>

    <div class="main__albums container">
      <div class="main__albums-item" v-for="album in albums" :key="album.id">
        <p class="main__albums-number">Album â„–: {{ album.id }}</p>
        <p class="main__albums-title">Album Title: {{ album.title }}</p>
      </div>
    </div>
  </main>

  
</template>

<script>
export default {
  data() {
    return {
      albums: null,
    };
  },
  methods: {
    async getUser() {
      try {
        const res = await fetch("https://jsonplaceholder.typicode.com/albums");
        const data = await res.json();
        this.albums = data;
      } catch (error) {}
    },
  },
  mounted() {
    setTimeout(() => {
      this.getUser();
    }, 2000);
  },
};
</script>

