<template>
  <main class="main" v-if="loader">
    <h2 class="title">Fake Photos</h2>
    <router-link class="home" to="/">Back to Home page</router-link>
    <div class="main__photos container">
      <div class="main__photos-item" v-for="photo in photos" :key="photo.id">
        <div class="main__photos-title">Title: {{ photo.title }}</div>
        <div class="main__photos-content">
          <img :src="photo.thumbnailUrl" :alt="photo.title" />
        </div>
      </div>
    </div>
  </main>

  <div class="main__loader" v-else>
    <div class="loader__el"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      photos: null,
      loader: false
    };
  },
  methods: {
    async getPhoto() {
      try {
        const res = await fetch("https://jsonplaceholder.typicode.com/photos");
        const data = await res.json();
        this.photos = data;
      } catch (error) {}
    },
  },
  mounted() {
    setTimeout(() => {
      this.getPhoto();
      this.loader = true;
    }, 2000);
  },
};
</script>

